<script>

import Snow from './components/Snow.vue'
import Portfolio_site from './components/Portfolio_site.vue'

export default {
  name: 'App',
  components: {
    Snow,
    Portfolio_site
  },
  data() {
    return {
      change_color_font_svg_snow: {
        color: '',
        fill: 'transparent',
        borderColor: '',
        backgroundColor: 'transparent'
      },
      change_color_background: '',
      portfolio_message: '★My portfolio★',
      portfolio_type: '',
      change_font_size_and_bold: {
        fontSize: '',
        fontWeight: ''
      }
    }
  },
  methods: {
    change() {
      this.color = Math.floor(Math.random() * 16777216).toString(16)
      this.change_color_background = this.color
      if(Math.floor(Math.random()*2)){
        this.change_color_background = '#000000'
      }else{
        this.change_color_background = '#ffffff'
      }
      this.color = '#000000'.slice(0, -this.color.length) + this.color
      if(Math.floor(Math.random()*2)){
        [this.color, this.change_color_background] = [this.change_color_background, this.color]
      }
      this.change_color_font_svg_snow.color = this.color
      this.change_color_font_svg_snow.fill = this.color
      this.change_color_font_svg_snow.borderColor = this.color
      this.nowColor = this.color
      this.change_color_font_svg_snow.backgroundColor = this.change_color_background
    },
    button1() {
      this.portfolio_message = 'Freedom Memo App'
      this.portfolio_type = 1
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    },
    button2() {
      this.portfolio_message = 'Work Management App'
      this.portfolio_type = 2
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    },
    button3() {
      this.portfolio_message = 'Ruby on Rails Tutorial'
      this.portfolio_type = 3
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    },
    button4() {
      this.portfolio_message = 'Macro Manage App muscle!!'
      this.portfolio_type = 4
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    },
    button5() {
      this.portfolio_message = 'First MarkDown Editor'
      this.portfolio_type = 5
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    },
    button6() {
      this.portfolio_message = 'Draggable MarkDown Editor!!'
      this.portfolio_type = 6
      this.change_font_size_and_bold.fontSize = '30px'
      this.change_font_size_and_bold.fontWeight = 'bold'
    }
  }
}
</script>

